import React, { PureComponent } from "react";
import CardPhone from "./ui-component/CardPhone";
import Specifications from "./ui-component/Specifications";
import "./style/main.css";

export default class Phone extends PureComponent {
  componentDidMount() {
    this.props.getOneItem(this.props.match.params.id);
  }

  render() {
    const { phoneCard, buyProduct } = this.props;
    return (
      <div className="phone-wrapper">
        <CardPhone {...phoneCard} buyProduct={buyProduct(phoneCard)} />
        <Specifications {...phoneCard} />
      </div>
    );
  }
}
