import * as constants from "./action-type";
import { addError } from "../error/action";

const addPhone = product => {
  return {
    type: constants.FETCH_PHONE_ID_SUCCESS,
    payload: product
  };
};

export function getPhoneId(id) {
  return dispatch => {
    let status;
    return fetch(`http://localhost:3002/api/products/${id}`)
      .then(data => {
        status = data.status;
        return data.json();
      })
      .then(data => {
        if (status >= 400 && status < 499) {
          dispatch(addError(status));
        }
        dispatch(addPhone(data));
      })
      .catch(error => dispatch(addError(error.message)));
  };
}
