import React, { PureComponent } from "react";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import "./style/main.css";
import BasketCard from "./BasketCard";

class Basket extends PureComponent {
  render() {
    const { basketList, removeProductFromBasket, totalPrice } = this.props;

    return (
      <>
        {!basketList.length ? (
          <div className="emptyBasket">Your basked is missing</div>
        ) : (
          <div className="totalSum">
            <Paper elevation={1}>
              <Typography component="p" className="totalContent">
                Total price: {totalPrice} $
              </Typography>
            </Paper>
          </div>
        )}
        <div className="baskedCard">
          {basketList.map(item => (
            <div className="baskedItem" key={item.id}>
              <BasketCard
                item={item}
                removeProductFromBasket={removeProductFromBasket}
              />
            </div>
          ))}
        </div>
      </>
    );
  }
}

export default Basket;
