import * as constants from "../action-type/constants";
import { addError } from "../../error/action";

const getItems = products => {
  return {
    type: constants.FETCH_PHONES_SUCCESS,
    payload: { products }
  };
};
const setPage = page => {
  return {
    type: constants.FETCH_NEXT_PAGE,
    payload: page
  };
};

export const showMore = () => {
  return {
    type: constants.SHOW_MORE_PRODUCTS
  };
};

export const fetchData = (limit, page) => dispatch => {
  return fetch(`http://localhost:3002/api/products?_limit=${limit}&_page=${page}`)
    .then(response => response.json())
    .then(data => {
      dispatch(getItems(data));
      if (data.length < limit) {
        dispatch(showMore());
        return;
      }
      dispatch(setPage(page + 1));
    })
    .catch(error => dispatch(addError(error.message)));
};
