import { connect } from "react-redux";
import Basket from "./Basket";
import { buyProduct, removeProductFromBasket } from "../../duks/Basket/action";
import totalPrice from '../../selectors/basketSelector'

const mapStateToProps = state => {
  return {
    basketList: state.basketReducer.basketList,
    totalPrice: totalPrice(state)
  };
};

const mapDispatchToProps = dispatch => {
  return {
    buyProduct: item => () => dispatch(buyProduct(item)),
    removeProductFromBasket: id => () => dispatch(removeProductFromBasket(id))
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Basket);
