import ProductList from "./ProductList";
import { connect } from "react-redux";
import { fetchData } from "../../duks/ProductList/actions/productList";
import { buyProduct } from "../../duks/Basket/action";

const mapStateToProps = state => ({
  products: state.productListReducer.listPhone,
  page: state.productListReducer.page,
  limit: state.productListReducer.limit,
  isDisabled: state.productListReducer.showMoreProducts,
  error: state.errorReducer.error
});

const mapDispatchToProps = dispatch => {
  return {
    fetchData: (limit, page) => dispatch(fetchData(limit, page)),
    buyProduct: item => () => dispatch(buyProduct(item))
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ProductList);
