import React, { Component } from "react";
import Button from "@material-ui/core/Button";
import Products from "./ui-component/Products";
import InformationMessage from "./ui-component/InformationMessage";

import "./style/main.css";
class ProductList extends Component {
  componentDidMount() {
    if (this.props.products.length === 0) {
      this.fetchNewProduct();
    }
  }

  fetchNewProduct = () => {
    this.props.fetchData(this.props.limit, this.props.page);
  };

  render() {
    const { products, isDisabled, buyProduct } = this.props;
    return (
      <div className="product-list-wrapper">
        <>
          <div className="product-list">
            {products.map(data => {
              return (
                <div className="product-item" key={data.id}>
                  <Products products={data} buyProduct={buyProduct(data)} />
                </div>
              );
            })}
          </div>
          <div className="show-more">
            <Button
              variant="contained"
              color="primary"
              onClick={this.fetchNewProduct}
              disabled={this.props.isDisabled}
            >
            Show More
            </Button>
            {isDisabled ? <InformationMessage/> : null}
          </div>
        </>
      </div>
    );
  }
}

export default ProductList;
