import * as constants from "./constants";

const initialState = {
  productsError: null
};

export default function errorRuducer(state = initialState, { payload, type }) {
  switch (type) {
    case constants.ADD_ERROR:
      return {
        productsError: payload
      };

    case constants.REMOVE_ERROR:
      return {
        productsError: null
      };

    default:
      return state;
  }
}
