import { connect } from "react-redux";
import Phone from "./Phone";
import { getPhoneId } from "../../duks/Product/action";
import { buyProduct } from "../../duks/Basket/action";

const mapStateToProps = state => {
  return {
    phoneCard: state.phonePageReducer.product,
    error: state.errorReducer.productsError
  };
};

const mapDispatchToProps = dispatch => {
  return {
    getOneItem: data => dispatch(getPhoneId(data)),
    buyProduct: item => () => dispatch(buyProduct(item))
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Phone);
