import * as constants from "./action-type";

const initialState = {
  basketList: []
};

export default (state = initialState, { payload, type }) => {
  switch (type) {
    case constants.ADD_PRODUCT_TO_BASKET:
      const item = state.basketList.find(item => item.id === payload.id);
      if (item) {
        return {
          ...state,
          basketList: state.basketList.map(item =>
            item.id === payload.id ? { ...item, count: item.count + 1 } : item
          )
        };
      }
      return {
        ...state,
        basketList: [...state.basketList, { ...payload, count: 1 }]
      };
    case constants.REMOVE_PRODUCT_FROM_BASKET:
      return {
        ...state,
        basketList: state.basketList.filter(item => item.id !== payload)
      };

    default:
      return state;
  }
};
