import * as constants from "../action-type/constants";

const initialState = {
  showMoreProducts: false,
  listPhone: [],
  page: 1,
  limit: 6
};

export default function itemReducer(state = initialState, action) {
  const { type, payload } = action;
  switch (type) {
    case constants.FETCH_PHONES_SUCCESS:
      return {
        ...state,
        listPhone: [...state.listPhone, ...payload.products]
      };
    case constants.SHOW_MORE_PRODUCTS:
      return {
        ...state,
        showMoreProducts: true
      };
    case constants.FETCH_NEXT_PAGE:
      return {
        ...state,
        page: payload
      };

    default:
      return state;
  }
}
